import { Component } from "react";
import { filter, title } from "./styles.module.css";

class FindInput extends Component {
  state = {
    filter: "",
  };
  handleChange = (event) => {
    const { name, value } = event.currentTarget;
    this.setState({ [name]: value }, () => this.props.onChange(this.state));
  };

  render() {
    return (
      <label className={title}>
        Find contacts by name:
        <input
          className={filter}
          // autocomplete="off"
          type="text"
          name="filter"
          value={this.state.filter}
          pattern="^[a-zA-ZÐ°-ÑÐ-Ð¯]+(([' -][a-zA-ZÐ°-ÑÐ-Ð¯ ])?[a-zA-ZÐ°-ÑÐ-Ð¯]*)*$"
          title="Ð˜Ð¼Ñ Ð¼Ð¾Ð¶ÐµÑ‚ ÑÐ¾ÑÑ‚Ð¾ÑÑ‚ÑŒ Ñ‚Ð¾Ð»ÑŒÐºÐ¾ Ð¸Ð· Ð±ÑƒÐºÐ², Ð°Ð¿Ð¾ÑÑ‚Ñ€Ð¾Ñ„Ð°, Ñ‚Ð¸Ñ€Ðµ Ð¸ Ð¿Ñ€Ð¾Ð±ÐµÐ»Ð¾Ð². ÐÐ°Ð¿Ñ€Ð¸Ð¼ÐµÑ€ Adrian, Jacob Mercer, Charles de Batz de Castelmore d'Artagnan Ð¸ Ñ‚. Ð¿."
          required
          onChange={this.handleChange}
        />
      </label>
    );
  }
}

export default FindInput;
